import React from 'react'
import { BrowserRouter, Routes,  Link } from 'react-router-dom'


let Home = (props: any) => (
  <div>
    <h1>Home</h1>
    <nav>
      <Link to={process.env.PUBLIC_URL + '/'}>Home</Link> |{" "}
      <Link to={process.env.PUBLIC_URL + 'dashboard'}>Dashboard</Link>
    </nav>
  </div>
)

let Dash = (props: any) => <div>Dash</div>

function App() {
  return (
    <div className="App">
      <BrowserRouter>
      <Routes>
        <Home path={process.env.PUBLIC_URL + '/'} />
        <Dash path={process.env.PUBLIC_URL + 'dashboard'} />
      </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
